@font-face {
  font-family: 'Icons';
  src: url("../../bower_components/semantic/dist/themes/default/assets/fonts/icons.eot");
  src: url("../../bower_components/semantic/dist/themes/default/assets/fonts/icons.eot?#iefix") format('embedded-opentype'), url("../../bower_components/semantic/dist/themes/default/assets/fonts/icons.woff2") format('woff'), url("../../bower_components/semantic/dist/themes/default/assets/fonts/icons.woff") format('woff'), url("../../bower_components/semantic/dist/themes/default/assets/fonts/icons.ttf") format('truetype'), url("../../bower_components/semantic/dist/themes/default/assets/fonts/icons.svg#icons") format('svg');
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-decoration: inherit;
  text-transform: none;
}
html,
body {
  height: 100%;
}
.ui.grid {
  min-height: 100%;
  height: 100%;
}
main#main {
  margin-top: 60px;
}
body,
a,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Source Sans Pro', sans-serif;
  color: dimgray;
}
.ui.list .list > .item a.header,
.ui.list > .item a.header {
  color: #DB2828!important;
}
.ui.list .list > .item a.header:hover,
.ui.list > .item a.header:hover {
  color: #b21e1e !important;
  text-decoration: underline;
}
#classes_filter {
  margin-bottom: 10px;
}
table#classes.display {
  max-width: 100%;
}
table#classes.display select {
  width: 50%;
}
iframe,
object,
embed {
  max-width: 100%;
}
table.em-calendar.fullcalendar {
  border: 1px solid #eee;
  width: 100%;
}
table.em-calendar.fullcalendar thead tr td {
  padding: 10px 0;
  color: white;
  text-transform: uppercase;
  text-align: center;
  border: 0;
}
table.em-calendar.fullcalendar thead tr td a {
  color: white;
}
table.em-calendar.fullcalendar tbody td {
  border: 1px solid #eee;
  height: 70px;
  vertical-align: top;
}
table.em-calendar.fullcalendar tbody td.eventless-today {
  color: white;
}
table.em-calendar.fullcalendar tbody td.eventful-today {
  color: white;
}
table.em-calendar.fullcalendar tbody td.eventful-today a {
  color: white;
}
table.em-calendar.fullcalendar tbody td.eventful-today a:hover {
  background: white;
}
table.em-calendar.fullcalendar tbody td.eventful-today ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
table.em-calendar.fullcalendar tbody tr.days-names td {
  height: 30px;
  width: 14%;
  border: 0;
  text-transform: uppercase;
  text-align: center;
  font-weight: bold;
  color: grey;
  padding: 10px 0;
}
table.em-calendar.fullcalendar tbody tr td.eventful {
  background-color: #eee;
}
table.em-calendar.fullcalendar tbody tr td.eventful ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
h1.logo a {
  color: white;
}
h1.logo a:hover {
  background: white;
  text-decoration: none;
}
.main-sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.main-sidebar ul li {
  font-size: 1.2em;
  margin: 10px 0 10px 30px;
}
.main-sidebar ul li a {
  color: white;
  text-transform: uppercase;
  font-weight: bold;
}
.main-sidebar ul li a:hover {
  background: white;
  text-decoration: none;
}
body {
  margin: 12px !important;
}
